/// Baseline for all margins and gaps on the site
/// @group configuration
/// @access public
/// @type Number
$baseline: 1.5em;


@import 'variables';
@import 'mixins';
@import 'ribbons';

body {
	background-color: #f2f2f2;
	padding: 10px;
	margin: 50px auto;
	width: $base-width;
	@media (min-width: 650px) {
		font-family: monospace; } }
p {
	$color: red;
	color: $color;
	text-align: center;
	@if $base-color == $color {
		font-size: 20px; }
	em {
		color: green; }
	@media (min-width: 650px) {
		color: pink; } }


// Listas - siempre comienzan en 1, a diferencia de los arrays
$colores: #ff0000, #000000, #0000ff, #f2f2f2;

b {
	color: nth($colores, 1);
	// funciÃ³n nth - permite acceder a un valor de la lista

	font-size: length($colores) + px;
	border-color: nth($colores, length($colores));
 }	// funcion length - devuelve un number igual a la cantidad de items de la lista


// funciones de listas
@function first($lista) {
	@return nth($lista, 1); }

@function last($lista) {
	@return nth($lista, length($lista)); }

p {
	$color: nth($colores, 3);
	color: $color;
	border-color: last($colores);
	@if $color == #ff0000 {
		display: block; }
	@else if $color == #000000 {
		display: none; }
	@else {
		display: inline-block; } }

$social: 'tw', 'fb', 'ig', 'yt';
$s-color: #29cdab, #26596c, #abb4bb, #e91737;
$s-number: 1;

@each $valor in $social {
	.#{$valor}-icon {
		color: nth($s-color, $s-number); }
	$s-number: $s-number + 1; }


$lista: 'red', 'green', 'blue';
@each $valor in $lista {
	.#{$valor} {
		color: #{$valor}; } }

// while
$divs: 5;
$divs-width: 5%;
$n: 5;

@while $divs > 0 {
	.el-#{$divs} {
		width: $divs-width + $n; }
	$divs: $divs - 1;
	$n: $n + 5; }

* {
	margin: 0;
	line-height: 1.5; }

ul {
	list-style: none;
	margin: 0;
	padding: 0; }

.container {
	max-width: 1200px;
	margin: auto; }

// https://coolors.co
$colors: #5bc0eb, #fde74c, #9bc53d, #373f51, #db504a;
$n: 1;

.list {
	li {
		font-style: italic;
		font-size: 1.6em;
		color: #FFFFFF;
		@include btn(5px) {
			box-shadow: 0 0 1px #b3b3b3; }
		@each $color in $colors {
			&:nth-child(#{length($colors)}n + #{$n}) {
				$the-color: nth($colors, $n);
				background-color: $the-color;
				@if $the-color == #fde74c {
					color: #222; }
				&:hover {
					background-color: lighten($the-color, 20); } }
			$n: $n + 1; } } }


$colors2: #319ebb, #751403, #3ee682, #e99c18, #83c4c0;
$n2: 1;
@for $i from 1 through length($colors2) {}
